.ProductDisplay{
    display: flex;
    margin: 0px 170px;
}
.productDisplay-left{
    display: flex;
    gap: 17px;

}
.product-display-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.product-display-img-list img{
    height: 163px;
}
.product-display-main-img{
    width: 586px;
    height: 700px;
}
.productDisplay-right{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;
}
.productDisplay-right h1{
    color: #3d3d3d;
    font-size: 40px;
    font-size: 700;
}
.productdisplay-right-star{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap:5px;
    color: #1c1c1c;
    font-size: 16px;

}
.productdisplay-right-prices{
    display: flex;
    margin: 40px 0px;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
}
.product-display-right-price-old{
    color: #818181;
    text-decoration: line-through;

}
.productdisplay-rightprice-new{
    color: #ff4141;
}
.productdisplay-rightsize h1{
    margin-top: 55px;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}
.product-display-rightsizes{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.product-display-rightsizes div{
    padding: 18px 24px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}
.productDisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
}
.productdisplay-right-cat{
    margin-top: 10px;

}
.productdisplay-right-cat span{
    font-weight: 600;
}
.productdisplay-right-dis{
    font-size: 17px;
    font-weight: 400;
}